<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item
        >来粉统计 / 控制台 / 转化分析 / 地域分析</el-breadcrumb-item
      >
    </el-breadcrumb>

    <div class="search">
      <div>
        <el-radio v-model="radio" label="1">近3天</el-radio>
        <el-radio v-model="radio" label="2">近7天</el-radio>
        <el-radio v-model="radio" label="3">近30天</el-radio>
      </div>
      <el-date-picker
        class="dateSpace"
        v-model="dateSpace"
        type="daterange"
        range-separator="至"
        value-format="yyyy-MM-dd"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      >
      </el-date-picker>
      <el-select
        v-model="search.site_id"
        placeholder="请选择网站"
        class="searchDomain"
      >
        <el-option
          v-for="item in domainArr"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-button class="searchBtn" @click="searchFn" type="primary"
        >搜索</el-button
      >
    </div>
    <div id="mapChart" v-if="tableData.length > 0"></div>

    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
    >
      <el-table-column
        type="index"
        label="序号"
        width="100px"
        :index="indexhandel"
      ></el-table-column>
      <el-table-column
        prop="province_name"
        label="省份"
        width="180"
      ></el-table-column>
      <el-table-column
        prop="city_name"
        label="地市"
        width="180"
      ></el-table-column>
      <el-table-column prop="ip_num" label="IP数"></el-table-column>
      <el-table-column prop="uv_num" label="UV数"></el-table-column>
      <el-table-column prop="pv_num" label="浏览量(PV)"></el-table-column>
      <el-table-column prop="change_num" label="转化数"></el-table-column>
      <div slot="empty" class="nodata">
        <img src="@/assets/noHaveData.jpg" alt="" />
        <p>暂无数据</p>
      </div>
    </el-table>
    <el-pagination
      v-if="tableData.length > 0"
      :page-size="search.page_size"
      :current-page.sync="search.page"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import utils from "@/mixin/watchDate.js";
import * as echarts from "echarts";
import "@/assets/chinaMap";
export default {
  mixins: [utils],
  data() {
    return {};
  },
  methods: {
    setChart(data) {
      let option = {
        tooltip: {
          formatter: function (a, b, c) {
            return a.name + "<br>测试时间：" + (a.value || "--") + "s";
          },
        },
        grid: {
          left: 0,
          top: 0,
          right: 0,
          bottom: 0,
        },
        visualMap: {
          left: 0,
          bottom: 0,
          pieces: [
            {
              gte: 5,
              label: "差",
              color: "#d50000",
            },
            {
              lt: 5,
              gte: 3,
              label: "较差(3s~5s)",
              color: "#ff6600",
            },
            {
              lt: 3,
              gte: 2,
              label: "警告(2s~3s)",
              color: "#ecec00",
            },
            {
              lt: 2,
              gte: 1,
              label: "较好(1s~2s)",
              color: "#00ff40",
            },
            {
              lt: 1,
              label: "好(<1s)",
              color: "#008000",
            },
          ],
          show: !0,
        },
        geo: {
          map: "china",
          label: {
            normal: {
              show: true,
              fontSize: "12",
              color: "rgba(0,0,0,.6)",
            },
          },
          itemStyle: {
            normal: {
              borderColor: "rgba(0, 0, 0, 0.1)",
            },
            emphasis: {
              areaColor: "rgba(0, 0, 0, .1)",
              shadowOffsetX: 0,
              shadowOffsetY: 0,
              borderWidth: 0,
            },
          },
        },
        series: [
          {
            name: "确诊病例",
            type: "map",
            geoIndex: 0,
            data: data,
          },
        ],
      };
      let a = echarts
        .init(document.getElementById("mapChart"))
        .setOption(option);
    },
    searchFn() {
      this.search.page = 1;
      this.getData();
    },
    getData() {
      this.axios.post("/v1/data/Area_change/index", this.search).then((res) => {
        if (res.data.code == 200) {
          this.tableData = res.data.result.list;
          this.total = res.data.result.page_info.total;
          let mapData = [];
          res.data.result.chart_data.forEach((e) => {
            let obj = {
              name: e.province_name.replace(/(市|省)$/, ""),
              value: e.uv_num,
            };
            mapData.push(obj);
            this.setChart(mapData);
          });
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.searchBtn {
  margin-left: 20px;
}
.search,
.dateSpace {
  margin: 20px 0;
}
#mapChart {
  width: 100%;
  height: 500px;
}
</style>